@font-face{font-family:'ProductSansRegular';src:url(src/assets/ProductSansRegular.ttf)}body,html{margin:0;padding:0;cursor:url(src/assets/mango.png),auto}body{width:100vw;height:100vh;background:#FFC324;background:-webkit-linear-gradient(to bottom, #FF8243, #FFC324);background:linear-gradient(to bottom, #FF8243, #FFC324);background-attachment:fixed;font-family:Roboto Slab, serif;color:#FFFB5A}::selection{background:none}::-moz-selection{background:none}h1,h2{font-family:ProductSansRegular, sans-serif;margin:0}h1{padding-top:20px;font-size:12.5vw}h2{margin-left:2px}.icon{-webkit-transition:opacity .5s;-moz-transition:opacity .5s;transition:opacity .5s;width:7.5vw;text-decoration:none;fill:#FFFB5A !important;opacity:1}.icon:hover{opacity:.75}#github{cursor:url(src/assets/banana.png),auto}#linkedin{cursor:url(src/assets/banana.png),auto}#instagram{cursor:url(src/assets/banana.png),auto}#soundcloud{cursor:url(src/assets/banana.png),auto}#twitter{cursor:url(src/assets/banana.png),auto}#paintmoji{cursor:url(src/assets/banana.png),auto}#particles-js{height:100vh;width:100vw;position:absolute;top:0px;left:0px;z-index:-100}p{width:65vw;font-size:5vw}a.link{color:#FFFB5A;text-decoration:none}a.link::before{content:'';position:absolute;bottom:0;left:0;width:100%;background:#44AF56;height:3vw;z-index:-1;transform:rotate(-1.5deg);transition:all 0.2s}a.link{color:inherit;position:relative;z-index:2;cursor:url(src/assets/mango.png),auto}a.link.on::before,a.link:hover::before{transform:rotate(0deg);height:6vw}.intro{height:100vh;display:flex;flex-direction:column}.content{margin-left:32.5vw;text-align:right;font-family:'Noto Sans', sans-serif}.header{margin-left:20px}.header{margin-bottom:30px}#canvas{width:100vw;height:100vh;position:absolute;top:0px}.animate{-webkit-animation:animate 0.5s cubic-bezier(0.2, 0.6, 0.2, 1);-moz-animation:animate 0.5s cubic-bezier(0.2, 0.6, 0.2, 1);animation:animate 0.5s cubic-bezier(0.59, 0.01, 0.28, 1);-webkit-animation-fill-mode:backwards;-moz-animation-fill-mode:backwards;animation-fill-mode:backwards}@-webkit-keyframes animate{0%{opacity:0;-webkit-transform:translateY(-50px)}100%{opacity:1;-webkit-transform:translateY(0%)}}@keyframes animate{0%{opacity:0;transform:translateY(-50px)}100%{opacity:1;transform:translateY(0%)}}.fade{-webkit-animation:fade 0.5s cubic-bezier(0.2, 0.6, 0.2, 1);-moz-animation:fade 0.5s cubic-bezier(0.2, 0.6, 0.2, 1);animation:fade 0.5s cubic-bezier(0.59, 0.01, 0.28, 1);-webkit-animation-fill-mode:backwards;-moz-animation-fill-mode:backwards;animation-fill-mode:backwards}@-webkit-keyframes fade{0%{opacity:0}100%{opacity:1}}@keyframes fade{0%{opacity:0}100%{opacity:1}}.fade2{-webkit-animation:fade 1s;-moz-animation:fade 1s;animation:fade 1s;-webkit-animation-fill-mode:backwards;-moz-animation-fill-mode:backwards;animation-fill-mode:backwards}@-webkit-keyframes fade2{0%{opacity:0}100%{opacity:1}}@keyframes fade2{0%{opacity:0}100%{opacity:1}}.delayheader1{-webkit-animation-delay:.5s;animation-delay:.5s}.delayheader2{-webkit-animation-delay:1s;animation-delay:1s}.delaycontent{-webkit-animation-delay:1.5s;animation-delay:1.5s}.delayparticle{-webkit-animation-delay:2s;animation-delay:2s}.delaycanvas{-webkit-animation-delay:2s;animation-delay:2s}.spacer{flex:1}html{visibility:visible;opacity:1}
